.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  background-color: #444;
  color: white;
}

.dialogBase {
  width: 500px;
  margin: auto;
  font-size: 20px;
}

.dialogText {
  border: 3px solid grey;
  min-height: 100px;
  text-align: left;
  padding: 10px;
  margin: auto;
  background-color: #111;
}
.characterName {
  border: 3px solid grey;
  border-bottom: 0;
  width: fit-content;
  padding: 5px;
  background-color: #222;
}
